export default {
	data() {
		return {
			myPhone: '',
			myNewPwd: '',
			myYzm: '',
			yzmBtnTitle:'获取短信验证码',
			timestatus:false,
		};
	},
	onLoad() {
		var _this = this;
		uni.getStorage({
			key: 'lastaccount',
			success: function(res) {
				console.log('getStoragelastaccount' + res.data);
				_this.myPhone = res.data;
			}
		});
	},

	methods: {
		clickOK() {
			if (this.myPhone.length == 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入手机号'
				});
				return;
			}

			if (this.myNewPwd.length == 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入账号密码'
				});
				return;
			}
			
			if (this.myYzm.length == 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入验证码'
				});
				return;
			}
			uni.showNavigationBarLoading();
			uni.request({
				url: this.mainServer + 'Home/User/login',
				data: {
					user_name: this.myPhone,
					password: this.myPwd
				},
				method: 'POST',
				success: res => {
					uni.hideNavigationBarLoading();
					console.log(res);
					uni.showToast({
						icon: 'none',
						title: res.data.message
					});
					// 本地存入 同步
					if (res.data.code == 1) {
						try {
							uni.setStorageSync('mid', res.data.mid);
							uni.setStorageSync('id', res.data.id);
							uni.setStorageSync('role', res.data.role);
							uni.setStorageSync('lastaccount', this.myPhone);
						} catch (e) {
							//TODO handle the exception
						}
						uni.navigateBack({});
					}
				},
				fail: res => {
					uni.hideNavigationBarLoading();
				}
			});
		},
		sendYzm(){
			if(this.myPhone.length==0){
				uni.showToast({
					icon:'none',
					title:"请输入手机号"
				});
				return;
			}
			
			uni.request({
				url:this.mainServer+'Home/Index/sms_send',
				data:{
					phone:this.myPhone,
					type:3
				},
				method:"POST",
				success: (res) => {
					console.log(res);
					uni.showToast({
						icon:"none",
						title:res.data.message
					});
					this.yzmBtnTitle=60;
					 this.timestatus = false
					       var interval = setInterval(() => {
					          --this.yzmBtnTitle
					       }, 1000)
					       setTimeout(() => {
					          clearInterval(interval)
					          this.timestatus = true;
							  this.yzmBtnTitle='重新获取';
					       }, 60000)
					
				}
			})
			
			
			
		},
		// getMyPersonalInfo(info_key) {
		// 	console.log('进来了'+info_key);
		// 	uni.getStorage({
		// 		key: info_key,
		// 		success: function(res) {
		// 			console.log('getMyPersonalInfo-MinePage.vue' + res.data);
		// 			return res.data;
		// 		},
		// 		fail(eeee) {
		// 			console.log('eeeeInfo-MinePage.vue' + eeee);
		// 		}
		// 	});
		// },
		
		
	}
};
